import {
	postOrderProduct,
	postOrderComment
} from "@/api/store";
import {
	trim
} from "@/utils";
import {
	VUE_APP_API_URL
} from "@/config";
import {
	required
} from "@/utils/validate";
import {
	validatorDefaultCatch
} from "@/utils/dialog";

const NAME = "GoodsEvaluate";

export default {
	name: NAME,
	components: {
		// VueCoreImageUpload
	},
	props: {},
	data: function() {
		return {
			orderCon: {
				cartProduct: {
					productInfo: {}
				}
			},
			scoreList: [{
					name: "商品质量",
					stars: ["", "", "", "", ""],
					index: -1
				},
				{
					name: "服务态度",
					stars: ["", "", "", "", ""],
					index: -1
				}
			],
			url: `${VUE_APP_API_URL}/api/qiNiuContent`,
			headers: {
				Authorization: "Bearer " + this.$store.state.token
			},
			uploadPictures: [],
			expect: "",
			unique: ""
		};
	},
	mounted: function() {
		this.unique = this.$yroute.query.id;
		this.getOrderProduct();
	},
	watch: {
		$yroute(n) {
			if (n.name === NAME && this.unique !== n.params.id) {
				this.unique = n.params.id;
				this.$set(this.scoreList[0], "index", -1);
				this.$set(this.scoreList[1], "index", -1);
				this.expect = "";
				this.uploadPictures = [];
				this.getOrderProduct();
			}
		}
	},
	methods: {
		getOrderProduct: function() {
			let that = this,
				unique = that.unique;
			postOrderProduct(unique).then(res => {
				that.orderCon = res.data;
			});
		},
		stars: function(indexn, indexw) {
			this.scoreList[indexw].index = indexn;
		},
		imageuploaded(res) {
			if (res.errno !== 0) {
				uni.showToast({
					title: res.msg || "上传图片失败",
					icon: 'none',
					duration: 2000
				});
				return
			}
			this.uploadPictures.push(res.data[0]);
		},
		async submit() {
			const expect = trim(this.expect),
				product_score =
				this.scoreList[0].index + 1 === 0 ? "" : this.scoreList[0].index + 1,
				service_score =
				this.scoreList[1].index + 1 === 0 ? "" : this.scoreList[1].index + 1;
			try {
				await this.$validator({
					product_score: [
						required("请选择商品质量分数", {
							type: "number"
						})
					],
					service_score: [
						required("请选择服务态度分数", {
							type: "number"
						})
					]
				}).validate({
					product_score,
					service_score
				});
			} catch (e) {
				return validatorDefaultCatch(e);
			}
			postOrderComment({
					productScore: product_score,
					serviceScore: service_score,
					unique: this.unique,
					pics: this.uploadPictures.join(","),
					comment: expect
				})
				.then(() => {
					uni.showToast({
						title: "评价成功",
						icon: "success",
						duration: 2000
					});
					this.$yrouter.push({
						path: "/pages/order/OrderDetails/index",
						query: {
							id: this.orderCon.orderId
						}
					});
				})
				.catch(err => {
					uni.showToast({
						title: err.msg || err.response.data.msg,
						icon: 'none',
						duration: 2000
					});
				});
		}
	}
};
